@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    color: #971111;
}

body {
    font: 400 14px 'Dancing Script', sans-serif;
    background: #F0F0F5;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Dancing+Script, sans-serif;
}

button {
    cursor: pointer;
}
.home-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}

.home-container section.home {
    width: 100%;
    max-width: 350px;
    margin-right: 10px;
}

.home-container section.home h1 {
    font-size: 32px;
    margin-top: 100px;
    margin-bottom: 32px;
}
